<template>
  
    <div class="repair-coordination-container">
      <h2 class="repair-panel-title">{{ createCarPanelIsOpened ? 'Створення нової машини' : 'Техніка' }} </h2>
      <div class="create-car-container" v-if="createCarPanelIsOpened">
        <div class="info-fields-container">
          <div class="car-info-field">
            <div class="custom-select" >
              <span class="car-create-subtitle">Модель: </span>
              <div class="selected-option" @click="openCloseCarStateSelect('select-models')" :class="[
                    selectFilterIsOpen === 'select-models' ? 'selected-option-is-active' : '',
                    (carCreateError && !createCarInfo.model) ? 'invalid-create-car-selects-true' : '']">
                <span class="select-value-text" v-if="(selectFilterIsOpen != 'select-models') && !selectFindModel" >{{ createCarInfo.model ? createCarInfo.model : 'Не визначена' }}</span>
                <input type="text" v-model="selectFindModel" :placeholder="(selectFilterIsOpen == 'select-models') ? (createCarInfo.model ? createCarInfo.model : 'Не визначена'): ''">
                <img src="../assets/images/dropdown-icon.png" :class="selectFilterIsOpen == 'select-models' ? 'dropdown-active-icon' : ''" alt="">
              </div>
              <div class="options-container" v-if="selectFilterIsOpen == 'select-models'">
                  <div class="option">
                    <img class="edit-icon" v-if="createCarModelIsOpen" @click="saveEditedModel(editModelValue, editModelValue)" src="../assets/images/confirm-icon.png" alt="">
                    <img class="add-model-image" v-if="!createCarModelIsOpen" @click="createCarModelIsOpen = true" src="../assets/images/add-image.png" alt="">
                    <input class="edit-model-input" v-if="createCarModelIsOpen" v-model="editModelValue" type="text">
                    <img class="edit-icon" v-if="createCarModelIsOpen" @click="createCarModelIsOpen = false" src="../assets/images/cancel-image.png" alt="">
                  </div>
                  <template v-for="(value, key) in filterCarModels" >
                    <div class="option" :key="key" >
                      <img src="../assets/images/edit-image.png" @click="enableEditCarModel(key)" v-if="editCarModelIsEnabled !=key" alt="">
                      <img class="edit-icon" @click="saveEditedModel(value, editModelValue)" src="../assets/images/confirm-icon.png" v-if="editCarModelIsEnabled == key" alt="">
                      <span class="car-model-name" @click="setCreatingCarModel(value)"  v-if="editCarModelIsEnabled !=key">{{ value }}</span>
                      <input class="edit-model-input" v-model="editModelValue" type="text" v-if="editCarModelIsEnabled == key">
                      <img src="../assets/images/delete-image.png" @click="deleteCarModel(value)" v-if="editCarModelIsEnabled !=key"  alt="">
                      <img class="edit-icon" @click="disableEditCarModel" src="../assets/images/cancel-image.png" v-if="editCarModelIsEnabled == key" alt="">
                    </div>
                  </template>
                </div>
            </div>
          </div>
          <label class="invalid-create-car-inputs" v-if="carCreateError && !createCarInfo.model">Виберіть або введіть модель машини</label>
          <div class="car-info-field">
            <span class="car-create-subtitle">Ліцензійний номер: </span>
            <input type="text" id="carLicenseNumber" maxlength="10" class="car-input-field" v-model="createCarInfo.licensePlate" :class="((carCreateError && !createCarInfo.licensePlate) || allCars[createCarInfo.licensePlate]) ? 'invalid-create-car-inputs-true' : ''">
          </div>
          <label class="invalid-create-car-inputs" for="carLicenseNumber" v-if="(carCreateError && !createCarInfo.licensePlate) || allCars[createCarInfo.licensePlate]">{{ allCars[createCarInfo.licensePlate] ? 'Машина з таким ліцензійним номером вже існує' : 'Введіть ліцензійний номер' }}</label>
          <div class="car-info-field">
            <span class="car-create-subtitle">Пробіг (км): </span>
            <input type="number" id="carMilage"  maxlength="7" class="car-input-field" v-model="createCarInfo.mileage" @input="validateMileage">
            
          </div>
          <div class="car-info-field">
            <div class="custom-select" >
              <span class="car-create-subtitle">Водій: </span>
              <div class="selected-option" @click="openCloseCarStateSelect('select-drivers-in-create')" :class="selectFilterIsOpen == 'select-drivers-in-create' ? 'selected-option-is-active' : ''">
                <div class="driver-info" v-if="showDriverInfo.uid && selectFilterIsOpen == 'select-drivers-in-create'">
                  <h2>Інформація про водія </h2>
                  <ul>
                    <li>ФІО: {{ showDriverInfo.lastname ? showDriverInfo.lastname + " " : ''}}{{showDriverInfo.firstname}}{{ showDriverInfo.surname ? " " + showDriverInfo.surname : ''}}</li>
                    <li>Пошта: {{ showDriverInfo.email }}</li>
                    <li>Номер: {{ showDriverInfo.phoneNumber ? showDriverInfo.phoneNumber : 'Не вказаний'}}</li>
                  </ul>
                </div>
                <span class="select-value-text" v-if="selectFilterIsOpen != 'select-drivers-in-create' && !selectFindDriver['select-drivers-in-create']" >{{ getDriverPlaceholderByUid(createCarInfo.driver) }}</span>
                <input type="text" v-model="selectFindDriver['select-drivers-in-create']" :placeholder="selectFilterIsOpen == 'select-drivers-in-create' ? getDriverPlaceholderByUid(createCarInfo.driver) : ''">
                <img src="../assets/images/dropdown-icon.png" :class="selectFilterIsOpen == 'select-drivers-in-create' ? 'dropdown-active-icon' : ''" alt="">
              </div>
              <div class="options-container" v-if="selectFilterIsOpen == 'select-drivers-in-create'">
                <div class="option" v-if="createCarInfo.driver" @click="selectCreatingCarDriver('')">
                  <span class="base-select-driver-option">Без водія</span> 
                </div> 
                <template v-for="driver in filteredDrivers" >
                  <div class="option" v-if="!driver.car" :key="driver.uid" >
                    <span class="driver-fio" @click="selectCreatingCarDriver(driver.uid)">{{ (driver.lastname ? driver.lastname + ' ': '') + driver.firstname + (driver.surname ? " "+driver.surname: '')}}</span> 
                    <button class="driver-info-button" @mouseover="setDriverInfo(driver)" @mouseout="setDriverInfo('')">?</button>
                  </div>
                </template>
                <div class="option" v-if="!filteredDrivers.length">
                  <span class="driver-fio">Співпадінь не знайдено</span> 
                </div>
              </div>
            </div>
          </div>
          <span class="invalid-create-car-inputs" v-if="carIsCreated">Готово!</span>
        </div>
        <div class="control-buttons-container">
          <button class="create-car" @click="createNewCar()">Створити</button>
          <button class="cansel-create" @click="closeCreatingCarPanel()">Назад</button>
        </div>
      </div>


      <div class="cars-container" v-if="!createCarPanelIsOpened">
        <div class="control-menu-container">
          <div class="buttons-wrapper">
            <div class="custom-select" >
              <div class="selected-option" @click="openCloseCarStateSelect('selected-search')" :class="selectFilterIsOpen == 'selected-search' ? 'selected-option-is-active' : ''"><span>{{displayCarStates(selectedCarState['selected-search'])}}</span><img src="../assets/images/dropdown-icon.png" :class="selectFilterIsOpen == 'selected-search' ? 'dropdown-active-icon' : ''" alt=""></div>
              <div class="options-container state-option-container" v-if="selectFilterIsOpen == 'selected-search'">
                <div class="option" v-if="selectedCarState['selected-search'] !='allCars'" @click="selectCarState('selected-search', 'allCars')"> <span class="state-option">Усі машини</span></div>
                <div class="option" v-if="selectedCarState['selected-search'] !='inUse'" @click="selectCarState('selected-search', 'inUse')"><span class="state-option">Працюючі машини</span></div>
                <div class="option" v-if="selectedCarState['selected-search'] !='sentToRepair'" @click="selectCarState('selected-search', 'sentToRepair')"><span class="state-option">Направлені на ремонт</span></div>
                <div class="option" v-if="selectedCarState['selected-search'] !='beingRepaired'" @click="selectCarState('selected-search', 'beingRepaired')"><span class="state-option">Ремонтуються</span></div>
              </div>
            </div>
            <button class="add-car-button" @click="createCarPanelIsOpened = true"><span>Додати машину</span> </button>
          </div>
          <input type="text" class="find-car-input" placeholder="Пошук..." v-model="searchQuery">
        </div>
        <div class="filtered-cars-container">
          <span v-if="!filteredCars.length && searchQuery">Співпадінь не знайдено</span>
          <template v-for="car in filteredCars">
            <div class="car-card" :key="car.licensePlate" v-if="(selectedCarState['selected-search'] == 'allCars') || (selectedCarState['selected-search'] == car.state)">
              <div class="confirmation-delete-car-window" v-if="deleteCarWindowIsOpen == car.licensePlate">
                <span class="delete-car-title">Ви дійно хочете видатили цю машину?</span>
                <div class="delete-car-buttons-container">
                  <button class="delete-car-button" @click="deleteCar(car.licensePlate)">Видалити</button>
                  <button class="cancel-delete-car-button" @click="deleteCarWindowIsOpen = ''">Відмінити</button>
                </div>
              </div>
              <div class="driver-info" v-if="showDriverInfo.uid && selectFilterIsOpen == car.licensePlate">
                <h2>Інформація про водія </h2>
                <ul>
                  <li>ФІО: {{ showDriverInfo.lastname ? showDriverInfo.lastname + " " : ''}}{{showDriverInfo.firstname}}{{ showDriverInfo.surname ? " " + showDriverInfo.surname : ''}}</li>
                  <li>Пошта: {{ showDriverInfo.email }}</li>
                  <li>Номер: {{ showDriverInfo.phoneNumber ? showDriverInfo.phoneNumber : 'Не вказаний'}}</li>
                </ul>
              </div>
              <div class="car-card-title">
                <h2>{{ car.model}}</h2>
                <div class="delete-button-container" @click="deleteCarWindowIsOpen = car.licensePlate">
                  <img src="../assets/images/delete-button-image.png" alt="">
                </div>
                
              </div>
              
              <ul>
                <li>Ліцензійний номер: {{ car.licensePlate }}</li>
                <li>Пробіг: {{ car.mileage}} км</li>
                <div class="car-driver-field">
                  <span>Водій: </span>
                  <div class="custom-select" >
                    <div class="selected-option" @click="openCloseCarStateSelect(car.licensePlate)" :class="selectFilterIsOpen == car.licensePlate ? 'selected-option-is-active' : ''">
                      <span class="select-value-text" v-if="selectFilterIsOpen != car.licensePlate && !selectFindDriver[car.licensePlate]" >{{ getSelectedOrExistingDriverName(car.licensePlate, car.driver) }}</span>
                      <input type="text" v-model="selectFindDriver[car.licensePlate]" :placeholder="selectFilterIsOpen == car.licensePlate ? getSelectedOrExistingDriverName(car.licensePlate, car.driver) : ''">
                      <img src="../assets/images/dropdown-icon.png" :class="selectFilterIsOpen == car.licensePlate ? 'dropdown-active-icon' : ''" alt="">
                      
                    </div>
                    <div class="options-container car-cars-option-container" v-if="selectFilterIsOpen == car.licensePlate">
                      <div class="base-select-option-container">
                        <div class="base-select-driver-option" v-if="car.driver && selectedCarDrivers[car.licensePlate] != 'withoutDriver'"  @click="selectCarDriver(car.licensePlate, 'withoutDriver')">Без водія</div> 
                        <div  class="base-select-driver-option" v-if="saveDriverChoiceButtonIsEnabled(car.driver, car.licensePlate)" @click="selectCarDriver(car.licensePlate, car.driver)">Відмінити вибір</div> 
                      </div>
                      <template v-for="driver in filteredDrivers" >
                        <div class="option" v-if="!driver.car" :key="driver.uid" >
                          <span class="driver-fio" @click="selectCarDriver(car.licensePlate, driver.uid)">{{ (driver.lastname ? driver.lastname + ' ': '') + driver.firstname + (driver.surname ? " "+driver.surname: '')}}</span> 
                          <button class="driver-info-button" @mouseover="setDriverInfo(driver)" @mouseout="setDriverInfo('')">?</button>
                        </div>
                      </template>
                      <div class="option" v-if="!filteredDrivers.length">
                        <span class="driver-fio">{{!filteredDrivers.length ? (selectFindDriver[car.licensePlate] ? 'Співпадінь не знайдено' : 'Немає вільних водіїв') : 'Співпадінь не знайдено'}}</span> 
                      </div>
                    </div>
                  </div>
                  <button class="save-car-driver-button" @click="saveSelectedCarDriver(car.driver, car.licensePlate)" v-if="saveDriverChoiceButtonIsEnabled(car.driver, car.licensePlate)">Зберегти</button>
                </div>
                <li>Стан машини: {{ displayCarStates(car.state)}}</li>
                <li v-if="car.repairBase">База ремонту: {{ car.repairBase }}</li>
              </ul>
            </div>
          </template>
        </div>
      </div>
    </div>
</template>
<script>
const carStates = {
  'allCars': 'Усі машини',
  'inUse': 'Працюючі машини',
  'sentToRepair': 'Направлені на ремонт',
  'beingRepaired': 'Ремонтуються',
}
export default {
name: 'RepairCoordinatorPanel',

props: {
  
},
data() {
    return {
      allCars: {},
      allCarModels: [],
      allDrivers: {},
      selectFilterIsOpen: '',
      searchQuery: '',
      selectedCarState: {'selected-search': 'allCars'},
      selectedCarDrivers: {},
      showDriverInfo: [],
      selectFindDriver: {},
      createCarPanelIsOpened: false,
      selectFindModel: '',
      createCarInfo: {
        model: '',
        licensePlate: '',
        mileage: null,
        repairMileage: this.mileage,
        state: 'inUse',
        driver: '',
        repairBase: ''
      },
      editModelValue: '',
      createCarModelIsOpen: '',
      editCarModelIsEnabled: false,
      carCreateError: false,
      carIsCreated: false,
      deleteCarWindowIsOpen: '',
    };
  },

  methods: {
    openCloseCarStateSelect(selectName){
      if(this.selectFilterIsOpen == selectName){
        this.selectFilterIsOpen = '';
      }else{
        this.selectFilterIsOpen = selectName;
      }

      if(this.carIsCreated){
        this.carIsCreated = false;
      }
    },
    selectCarState(selectName, filterName){
      this.selectedCarState[selectName] = filterName;
      this.selectFilterIsOpen = '';
    },
    selectCarDriver(carLicensePlate, driverUid){
      this.selectedCarDrivers[carLicensePlate] = driverUid;
      this.selectFindDriver[carLicensePlate] = '';
      this.selectFilterIsOpen = '';
    },
    displayCarStates(state){
      return carStates[state];
    },
    setDriverInfo(driver){
      this.showDriverInfo = driver;
    },
    getDriverPlaceholderByLicensePlate(licensePlate) {
      const selectedDriverId = this.selectedCarDrivers[licensePlate];
      const selectedDriver = this.allDrivers[selectedDriverId];

      if (selectedDriver) {
        const { firstname, lastname, surname } = selectedDriver;
        return `${lastname ? lastname + ' ' : ''}${firstname}${surname ? ' ' + surname : ''}`;
      } else {
        return 'Без водія';
      }
    },
    getDriverPlaceholderByUid(uid){
      const selectedDriver = this.allDrivers[uid];
      if (selectedDriver) {
        const { firstname, lastname, surname } = selectedDriver;
        return `${lastname ? lastname + ' ' : ''}${firstname}${surname ? ' ' + surname : ''}`;
      } else {
        return 'Без водія';
      }
    },
    getSelectedOrExistingDriverName(licensePlate, driverUid){
      if(this.selectedCarDrivers[licensePlate]){
        return this.getDriverPlaceholderByLicensePlate(licensePlate)
      }
      if(driverUid){
        return this.getDriverPlaceholderByUid(driverUid);
      }
        
      return 'Без водія';
    },
    enableEditCarModel(key){
      this.editCarModelIsEnabled = key;
      this.editModelValue = key;
    },
    disableEditCarModel(){
      this.editCarModelIsEnabled = '';
      this.editModelValue = '';
    },
    setCreatingCarModel(model){
      this.createCarInfo.model = model;
      this.selectFilterIsOpen = ''
    },
    saveEditedModel(value, newValue){
      if(this.allCarModels[value])
        delete this.allCarModels[value];
      this.$set(this.allCarModels, newValue, newValue);
      this.editCarModelIsEnabled = '';
      this.editModelValue = ''
      return this.$store.dispatch('saveNewModelsCollection', this.allCarModels);
    },
    deleteCarModel(value){
      this.$delete(this.allCarModels, value);
      return this.$store.dispatch('saveNewModelsCollection', this.allCarModels);
    },
    selectCreatingCarDriver(diverUid){
      this.createCarInfo.driver = diverUid;
      this.selectFilterIsOpen = '';
    },
    createNewCar(){
      if(this.carIsCreated){
        this.carIsCreated = false;
      }

      if(!this.createCarInfo.model || this.allCars[this.createCarInfo.licensePlate] || !this.createCarInfo.licensePlate){
        this.carCreateError = true;
      }else{
        this.createCarInfo.repairMileage = this.createCarInfo.mileage;

        if(this.allCarModels[this.createCarInfo.model])
          delete this.allCarModels[this.createCarInfo.model];

        this.$set(this.allCarModels, this.createCarInfo.model, this.createCarInfo.model);
        this.$set(this.allCars, this.createCarInfo.licensePlate, this.createCarInfo);
        this.$store.dispatch('createNewCar', this.createCarInfo);

        if(this.createCarInfo.driver)
          this.$store.dispatch('updateUserCar', this.createCarInfo);

        this.createCarInfo = {
          model: '',
          licensePlate: '',
          mileage: null,
          repairMileage: this.mileage,
          state: 'inUse',
          driver: '',
          repairBase: ''
        }
        this.createCarModelIsOpen = false;
        this.carCreateError = false;
        this.carIsCreated = true;
      }
    },
    closeCreatingCarPanel(){
      this.createCarPanelIsOpened = false;
      this.carIsCreated = false;
      this.carCreateError = false;
      this.createCarInfo = {
          model: '',
          licensePlate: '',
          mileage: null,
          repairMileage: this.mileage,
          state: 'inUse',
          driver: '',
          repairBase: ''
      }
    },
    deleteCar(licensePlate) {
      this.$store.dispatch('deleteCar', licensePlate);

      if (this.allCars[licensePlate].driver) {
        this.$store.dispatch('updateUserCar', { driver: this.allCars[licensePlate].driver, licensePlate: '' });
        this.$delete(this.allDrivers, this.allCars[licensePlate].driver);
      }
      if (this.selectedCarDrivers[licensePlate]) {
        this.$delete(this.selectedCarDrivers, licensePlate);
      }
      if (this.selectFilterIsOpen === licensePlate) {
        this.selectFilterIsOpen = '';
      }
      this.$delete(this.allCars, licensePlate);
      this.deleteCarWindowIsOpen = ''
    },
    validateMileage() {
      this.createCarInfo.mileage = this.createCarInfo.mileage.replace(/\D/g, '').slice(0, 7);
    },
    saveDriverChoiceButtonIsEnabled(driver, licensePlate){
      if(this.selectedCarDrivers[licensePlate] && this.selectedCarDrivers[licensePlate] == 'withoutDriver' && driver == ''){
        return false;
      }else if(this.selectedCarDrivers[licensePlate] && this.selectedCarDrivers[licensePlate] != driver){
        return true;
      }else{
        return false
      }
    },
    saveSelectedCarDriver(oldDriver, licensePlate){
      if(oldDriver){
        this.$store.dispatch('updateUserCar', {driver: oldDriver, licensePlate: ''});
        this.$set(this.allDrivers[oldDriver], 'car', '');
      }

      if(this.selectedCarDrivers[licensePlate] != 'withoutDriver'){
        this.$store.dispatch('updateUserCar', {driver: this.selectedCarDrivers[licensePlate], licensePlate: licensePlate});
        this.$store.dispatch('updateCarDriver', {driver: this.selectedCarDrivers[licensePlate], licensePlate: licensePlate });
        this.$set(this.allDrivers[this.selectedCarDrivers[licensePlate]], 'car', licensePlate);
        this.$set(this.allCars[licensePlate], 'driver', this.selectedCarDrivers[licensePlate]);
        this.$delete(this.selectedCarDrivers, licensePlate);
      }else{
        console.log(this.selectedCarDrivers[licensePlate])
        this.$store.dispatch('updateCarDriver', {driver: '', licensePlate: licensePlate });
        this.$set(this.allCars[licensePlate], 'driver', '');
        this.$delete(this.selectedCarDrivers, licensePlate);
      }
    },
    
  },

  computed: {
    filteredDrivers() {
      var foundDrivers;
      if(this.selectedCarDrivers && this.selectFindDriver[this.selectFilterIsOpen]){
        foundDrivers = Object.values(this.allDrivers).filter(driver => {
            const fullName = (driver.lastname ? driver.lastname + ' ' : '') + driver.firstname + (driver.surname ? " " + driver.surname : '');
            const isDriverSelected = Object.values(this.selectedCarDrivers).includes(driver.uid);
            return fullName.toLowerCase().includes(this.selectFindDriver[this.selectFilterIsOpen].toLowerCase()) && !isDriverSelected && !driver.car;
          });
        console.log(foundDrivers);
      }else if(this.selectFindDriver[this.selectFilterIsOpen]){
        foundDrivers = Object.values(this.allDrivers).filter(driver => {
          const fullName = (driver.lastname ? driver.lastname + ' ': '') + driver.firstname + (driver.surname ? " "+driver.surname: '');
          return fullName.toLowerCase().includes(this.selectFindDriver[this.selectFilterIsOpen].toLowerCase() && !driver.car);
        });
        console.log(foundDrivers);
      }else if(this.selectedCarDrivers){
        foundDrivers = Object.values(this.allDrivers).filter(driver => {
          return !Object.values(this.selectedCarDrivers).includes(driver.uid) && !driver.car;
        });
        console.log(foundDrivers);
      }
      else{
        foundDrivers = Object.values(this.allDrivers).filter(driver => !driver.car);
      }
      return foundDrivers; 
    },
    filterCarModels(){
      const query = this.selectFindModel.toLowerCase();
      return Object.fromEntries(
        Object.entries(this.allCarModels).filter(([key]) => key.toLowerCase().includes(query))
      );
    },
    filteredCars() {
      const query = this.searchQuery.toLowerCase().trim();

      if (!query) {
        return this.allCars;
      }

      return Object.values(this.allCars).filter(car => {
        // Исключаем поля mileage и repairMileage из поиска
        const searchableFields = ['model', 'repairBase', 'licensePlate', 'state', 'driver'];

        return searchableFields.some(field => {
          if (field === 'driver') {
            const driverName = this.getDriverPlaceholderByUid(car[field]);
            return driverName.toLowerCase().includes(query);
          } else {
            return car[field] && car[field].toString().toLowerCase().includes(query);
          }
        });
      });
    },
    
  },

  mounted() { 
    this.$store.dispatch('getAllCars').then((querySnapshot)=>{
      querySnapshot.forEach((result) => {
        var car = result.data(); 
        this.$set(this.allCars, car.licensePlate, car);
        this.selectedCarState[car.licensePlate] = car.state;
      });
      
    });
    this.$store.dispatch('getAllDrivers').then((querySnapshot)=>{
      querySnapshot.forEach((result) => {
        var driver = result.data();
        this.$set(this.allDrivers, driver.uid, driver);
      });
      console.log(this.allDrivers)
    });
    this.$store.dispatch('getAllCarsModels').then((result)=>{
      this.allCarModels = result.data()
    });
    
  },
  }
</script>